          description: '成为全球领先的智慧农业解决方案提供商，通过技术创新改变传统农业生产方式。'
        }
      ],
      coreValues: [
        {
          icon: 'fas fa-lightbulb',
          title: '创新',
          description: '持续技术创新，引领行业发展'
        },
        {
          icon: 'fas fa-award',
          title: '专业',
