            '精准喷洒，减少农药使用量30%以上',
            '自动避障，安全可靠',
            '智能路径规划，提高作业效率'
          ]
        },
        {
          icon: 'fas fa-chart-bar',
          title: '农业大数据分析',
          description: '基于多源数据采集与分析，提供科学决策支持',
          features: [
            '实时监测作物生长状态',
            '预测产量与病虫害风险',
